
*{
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden!important;
}
.slide-left-enter,
.slide-right-leave-active {
  -webkit-transform: translate(800px, 0);
          transform: translate(800px, 0);
  transition: 0.4s;
}
.slide-left-leave-active,
.slide-right-enter {
  -webkit-transform: translate(-800px, 0);
          transform: translate(-800px, 0);
  transition: 0.4s;
}
.layout .main-content {
  max-width: 600px;
  min-height: calc(100vh-60px);
  margin: 0 auto;
}
.el-tabs__header {
  margin: 0;
}



